import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import AssetContext from '../../context/AssetContext.js'

import TypeContext from '../../context/TypeContext.js'


export default function FloorAsset() {

  return (
    <div>
        <SelectAsset/>
    </div>
  );
}



function SelectAsset() {
  // const [asset, setAsset] = React.useState('');
  const {asset,setAsset} = React.useContext(AssetContext)

  const {type,setType} = React.useContext(TypeContext)

  const handleChange = (event) => {
    setAsset(event.target.value);
  };

  return (
    <div>
      <FormControl sx={{ m: 1, width: '330px'}}>
        <InputLabel id="demo-simple-select-autowidth-label"
        sx={{
          color: "black",
          
        }}>{type=='Buy'?"Получаете":"Отдаёте"}</InputLabel>
        <Select
          labelId="demo-simple-select-autowidth-label"
          id="demo-simple-select-autowidth"
          value={asset}
          onChange={handleChange}
          autoWidth
          label={type=='Buy'?"Получаете":"Отдаёте"}
          sx={{
            
          }}
        >
          <MenuItem value={'BTC'}>BTC</MenuItem>
          <MenuItem value={'ETH'}>ETH</MenuItem>
          <MenuItem value={'BNB'}>BNB</MenuItem>
          <MenuItem value={'USDT'}>USDT</MenuItem>

        </Select>
      </FormControl>
    </div>
  );
}
