import React,{useContext} from 'react';

import Button from 'react-bootstrap/Button';
import Stack from '@mui/material/Stack';

import SceneContext from '../context/SceneContext.js'
import TypeContext from '../context/TypeContext.js'
import AssetContext from '../context/AssetContext.js'
import TypePayContext from '../context/TypePayContext.js'

export default function NavBar() {
  const {scene,setScene} = useContext(SceneContext)
  const {type,setType} = useContext(TypeContext)
  const {asset,setAsset} = useContext(AssetContext)
  const {typePay,setTypePay} = useContext(TypePayContext)

  return (
    <Stack direction="row" spacing={2} className="d-flex justify-content-center">
      <Button variant="outline-dark" size="lg" onClick={
          ()=>{
            setScene(1);
            sessionStorage.setItem('type','Buy');
            setType('Buy');
            setAsset('');
            setTypePay('');
          }
        } style={{width:'100vh', maxWidth: 400}}>Купить</Button>
      <Button variant="outline-dark" size="lg" onClick={
        ()=>{
            setScene(1);
            sessionStorage.setItem('type','Sell');
            setType('Sell');
            setAsset('');
            setTypePay('');
          }
        } style={{width:'100vh', maxWidth: 400}}>Продать</Button>
    </Stack>
  );
} 
