import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

import TypePayContext from '../../context/TypePayContext.js'
import TypeContext from '../../context/TypeContext.js'


export default function FloorTypePay() {
  
  return (
    <div>
        <TypePay />
    </div>
  );
}




function TypePay() {
  // const [typePay, setTypePay] = React.useState('');
  const {typePay,setTypePay} = React.useContext(TypePayContext)
  const {type,setType} = React.useContext(TypeContext)

  const handleChange = (event) => {
    setTypePay(event.target.value);
  };

  return (
    <div>
      <FormControl sx={{ m: 1,width: '330px'}}>
        <InputLabel id="demo-simple-select-autowidth-label" sx={{
            color: "black",
            
          }} >{type=='Sell'?"Получаете":"Отдаёте"}</InputLabel>
        <Select
          labelId="demo-simple-select-autowidth-label"
          id="demo-simple-select-autowidth"
          value={typePay}
          onChange={handleChange}
          autoWidth
          label={type=='Sell'?"Получаете":"Отдаёте"}
          sx={{
            color: "black",
            borderColor:"black"
          }}
        >
          <MenuItem value={10}>Сбербанк</MenuItem>
          <MenuItem value={21}>Тиньков</MenuItem>
          <MenuItem value={22}>QIWI</MenuItem>
          <MenuItem value={23}>YandexMoney</MenuItem>

        </Select>
      </FormControl>
    </div>
  );
}
