import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import Container from 'react-bootstrap/Container';

import NavBar from '../component/NavBar.js'
import SelectAsset from '../component/sc1/SelectAsset.js'
import SelectTypePay from '../component/sc1/SelectTypePay.js'
import InputAsser from '../component/sc1/InputAsser.js'
import InputFiat from '../component/sc1/InputFiat.js'

import Button from 'react-bootstrap/Button';
import SceneContext from '../context/SceneContext.js'
import TypeContext from '../context/TypeContext.js'
import AssetContext from '../context/AssetContext.js'
import TypePayContext from '../context/TypePayContext.js'
import React,{useContext,useState,useEffect} from 'react';


export default function Scene1(){

    const {scene,setScene} = useContext(SceneContext)
    const {type,setType} = useContext(TypeContext)
    const {asset,setAsset} = useContext(AssetContext)
    const {typePay,setTypePay} = useContext(TypePayContext)

    const [flagInput,setFlagInput] = useState(false)
    // const flagInput=

    // if(asset && typePay){
    //   setFlagInput(true)
    // }else{
    //   setFlagInput(false)
    // }
    useEffect(()=>{
      if(asset && typePay){
        
        setFlagInput(true)
        
      }else{
        setFlagInput(false)
      }
    },[asset,typePay])
    return(
      <>
        <Container style={{height: '20%'}}>
        
          <Row style={{paddingTop:"5vh"}}>
            <div className="d-flex justify-content-center">
              <h1><b onClick={()=>{
                setScene(0);
                setAsset('');
                setTypePay('');
                setType();
                }}>Cherdak.fun</b></h1>
            </div>
          </Row>
          <NavBar />  
        </Container>
        <Container style={{height: '60%'}} className="d-flex align-items-center">
          <Container>
            
            <Row>
              <Col>
                  <div className="d-flex justify-content-center">
                    {
                      <SelectAsset/>
                    }
                  </div>
              </Col>
            </Row>
            <Row>
              <Col>
              <div className="d-flex justify-content-center">
                {
                  flagInput
                    ?<InputAsser/>              
                    :null
                }
                </div>
              </Col>
            </Row>
            <Row>
              <Col>
                <div className="d-flex justify-content-center">
                    {
                      <SelectTypePay/>
                    }
                </div>
              </Col>
            </Row>
            <Row>
              <Col>
              <div className="d-flex justify-content-center">
                { 
                  flagInput
                    ?<InputFiat/>              
                    :null
                 
                }
              </div>
              </Col>
            </Row>
            <Row>

              <Col>
              <div style={{display:flagInput?"block":'none'}}>
                <div className="d-flex justify-content-center">
                  <Button variant="outline-dark" size="lg" onClick={()=>{setScene(2)}}style={{width:'100vh', maxWidth: 330}}>Далее</Button>
                </div>
              </div>
              </Col>
            </Row>
          </Container>
        </Container>
      </>
    )
}